import { useTranslation } from "react-i18next";
import CustomSelectUniversalBatch from "../../Ui/Select/CustomSelectUniversalBatch";
import { useInstituteAllUniversalBatch } from "../../Hooks/ApiHooks/DepartmentManager/department-manager-api";

const InstituteUniversalBatchDorpdown = ({
  instituteId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const { instituteAllUniversalBatch } = useInstituteAllUniversalBatch({
    data: {
      id: instituteId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !instituteId,
  });
  return (
    <>
      <CustomSelectUniversalBatch
        selectLabel={t("select_universal_batch")}
        selectedValue={t("select_universal_batch_placeholder")}
        options={
          instituteAllUniversalBatch?.list?.length > 0
            ? instituteAllUniversalBatch?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />

      {/* {instituteAllUniversalBatchLoading && <Loading />} */}
    </>
  );
};

export default InstituteUniversalBatchDorpdown;
